@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;font-family:'Orbitron', sans-serif;display:flex;justify-content:center;align-items:center;background:#EAEBEC;height:100vh}.calculator{width:360px;box-shadow:0 8px 50px -7px black;border:solid 1px #3A4655}.calculator-display{height:70px;padding:20px;display:flex;justify-content:flex-end;align-items:flex-end;background:#3A4655}.calculator-display .display-inner{font-size:2.5em;margin:10px;color:white;overflow:hidden}.button-wrapper{display:flex;flex-wrap:wrap;justify-content:center;background:#425062}.button-wrapper button{background:#425062;font-family:'Orbitron', sans-serif;color:white;border:none;cursor:pointer;border-right:solid 2px #3C4857;border-bottom:solid 2px #3C4857;font-size:2em;height:75px;width:25%;transition:all 0.2s}.button-wrapper button.active{background-color:#E0B612}.button-wrapper button.blank{height:0;visibility:hidden;border:none}.button-wrapper button.operation{color:lightblue;background-color:#404D5E}.button-wrapper button#equals{background:#425062;color:#fff}.button-wrapper button#clear{height:50px;align-self:flex-end;color:#D95D4E;width:100%}.button-wrapper button#clear.active{background-color:#E0B612;color:#fff}

